@import url("./css/theme_light.css");
@import url("./css/theme_dark.css");

* {
  margin: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  min-height: 100dvh;
  background: var(--background);
  background-color: var(--background);
  color: var(--text);
  touch-action: manipulation;
}

button {
  outline: none;
  padding: 5px 10px;
  border-radius: 10px;
  color: var(--text);
  border: 1px solid var(--textDim);
  background: var(--buttonInitBgColor);
}

button:disabled {
  border-color: var(--buttonDisabledBorderColor);
  color: var(--textDim);
  background: var(--secondary);
}

.app {
  padding-bottom: 5rem;
}