.root {
  width: 100%;
  max-width: 700px;
  margin: auto;
  padding: 15px;
}

.lessons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.buttons {
  position: fixed;
  background: var(--primary);
  bottom: 0;
  left: 0;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 5px;
  padding: 10px;
  border-top: 1px solid var(--secondary);
}

.button {
  position: relative;
  padding: 15px 0;
  border-color: var(--secondary);
}

.buttonActive {
  border-color: var(--text);
}

.todayPoint {
  --padding: 4px;
  position: absolute;
  top: var(--padding);
  right: var(--padding);
  padding: 5px;
  border-radius: 50%;
  background-color: var(--accent);
}